import React, {Component }from 'react';
import './App.css';
import { EmployeeService } from './services/EmployeeService';
import {DataTable} from 'primereact/datatable'
import { Column } from 'primereact/column';
import { Panel } from 'primereact/panel';

import "primereact/resources/themes/lara-light-indigo/theme.css";  //theme
import "primereact/resources/primereact.min.css";                  //core css
import "primeicons/primeicons.css";       
import { Avatar } from 'primereact/avatar';
import { AvatarGroup } from 'primereact/avatargroup';  
import {Menubar} from 'primereact/menubar';  
import {Dialog} from 'primereact/dialog';    
import { InputText } from 'primereact/inputtext';
               //icons
 

export default class App extends Component{
  constructor(){
    super();
    this.state = {};
    this.items = [
      {
        label : 'Nuevo',
        icon  : 'pi pi-fw pi-plus',
        command : () => {this.showSaveDialog()}
      },
      {
        label : 'Editar Admin',
        icon  : 'pi pi-fw pi-pencil',
        command : () => {this.showEditDialog()}
      },
      {
        label : 'Editar Krugeriano',
        icon  : 'pi pi-fw pi-pencil',
        command : () => {this.showEditDialog()}
      },
      {
        label : 'Eliminar',
        icon  : 'pi pi-fw pi-trash',
        command : () => {this.delete()}
      }
    ];
    this.employeeService = new EmployeeService();
  }
  



  componentDidMount(){
    this.employeeService.getAll().then(data=>this.setState({employees: data}))
    this.setState({
      visible : false,
      krugeriano:{
        ID:null,
        cedula:null,
        Nombres:null,
        Apellidos:null,
        Email:null
      }
    })
  }

  render(){
    return(

      <div>
  
       <div style={{width:'10%', marginTop:'10px'}}>
        <Avatar label="SA" shape="circle" size="large" ></Avatar>SUPER ADMIN
      </div>
      
     
      

      <div style={{width:'90%', marginTop:'20px', margin:' auto'}}>

      
 
        <Panel header="Krugerianos Inmunizados" >
        <Menubar model={this.items}/>
        <br/>
          <DataTable value={this.state.employees} >
            <Column field='employeeId' header='ID'></Column>
            <Column field='employeeDni' header='Cedula'></Column>
            <Column field='employeeName' header='Nombres'></Column>
            <Column field='employeeLastName' header='Apellidos'></Column>
            <Column field='employeeEmail' header='Email'></Column>
            <Column field='employeeBirthday' header='Fecha Nacimiento'></Column>
            <Column field='employeeAddress' header='Direccion'></Column>
            <Column field='employeeCellphone' header='Celular'></Column>
            <Column field='employeeVaccineState' header='Estado Vacunacion'></Column>
            <Column field='employeeVaccineType' header='Tipo Vacuna'></Column>
            <Column field='employeeVaccineDate' header='Fecha Ultima Vacuna'></Column>
            <Column field='employeeVaccineDosis' header='Dosis'></Column>
            <Column field='userId' header='userId'></Column>
          </DataTable>
        </Panel>
      </div>

      <Dialog header="Crear Krugeriano" visible={this.state.visible} style={{width:'60%'}} modal={true} onHide={() => this.setState({visible: false})}>
     
            
      </Dialog>
     
      
      </div>
    );
  }  

  showEditAdminModal(){
    this.setState({
      visible:true
    })
  }
}